import React, { useEffect, useState } from "react";
import Map from "./components/Map";
import Table from "./components/Table";
import { Button, Checkbox, notification, Select, Space, Tabs } from "antd";
import { useCSVReader } from "react-papaparse";
import { UploadOutlined } from "@ant-design/icons";
import cn from "classnames";
import LatLon from "geodesy/latlon-ellipsoidal-vincenty.js";

const { TabPane } = Tabs;
const { Option } = Select;

const filterData = (rows = [], distanceRange, signalRange) => {
  const data = [];
  const columns = {};

  rows.forEach((row, index) => {
    row.columns.forEach(({ value }, index) => {
      if (Number(value) % 1 !== 0 || Number(value) > Number(signalRange)) {
        columns[index] = 1;
      }
    });

    if (rows[index + 1]) {
      const point1 = new LatLon(
        parseFloat(rows[index].columns[1].value),
        parseFloat(rows[index].columns[2].value)
      );
      const point2 = new LatLon(
        parseFloat(rows[index + 1].columns[1].value),
        parseFloat(rows[index + 1].columns[2].value)
      );

      if (point1.distanceTo(point2) < distanceRange) {
        data.push(row);
      }
    } else {
      data.push(row);
    }
  });

  return [
    data
      .sort((a, b) => a.order - b.order)
      .map((data) => ({
        ...data,
        columns: data.columns.filter((column, index) => columns[index]),
      })),
    columns,
  ];
};

const App = () => {
  const { CSVReader } = useCSVReader();
  const [files, setFiles] = useState({});

  const [data, setData] = useState({});
  const [header, setHeader] = useState({});
  const [distanceRange, setDistanceRange] = useState(10);
  const [signalRange, setSignalRange] = useState(10);
  const [distance, setDistance] = useState(25);
  const [selected, setSelected] = useState({});
  const [showSignal, setShowSignal] = useState(false);

  const onUploadAccepted = ({ data }, file) => {
    let rows = [];
    const [header, ...lines] = data;

    lines.forEach((line) => {
      if (
        line.length === header.length &&
        line.every(
          (value) =>
            String(value).trim() !== "" && String(value).trim() !== "null"
        )
      ) {
        rows.push({
          id: (Math.random() + 1).toString(36).substring(7),
          columns: line.map((value) => {
            return { value };
          }),
        });
      }
    });

    rows = rows.sort((a, b) => a.distance - b.distance);
    if (rows.length > 0) {
      // notification.success({
      //   message: file.name,
      //   description:
      //     new Intl.NumberFormat("ua-UA").format(rows.length - 1) + " rows",
      // });

      setFiles((state) => ({
        ...state,
        [file.name]: { rows, header },
      }));
    } else {
      notification.error({
        message: file.name,
        description: "valid rows not found",
      });
    }
  };

  useEffect(() => {
    const data = {};
    const filteredColumns = {};
    Object.keys(files).forEach((name) => {
      const [rows, columns] = filterData(
        files[name].rows,
        distanceRange,
        signalRange
      );
      data[name] = rows;
      filteredColumns[name] = columns;
    });

    const header = {};
    Object.keys(files).forEach((name) => {
      header[name] = (files[name]?.header || []).filter(
        (_, index) => filteredColumns[name][index]
      );
    });

    setHeader(header);
    setData(data);
    setSelected((selected) => {
      const newState = {};
      Object.keys(files).forEach((name) => {
        newState[name] = (selected[name] || []).filter((id) =>
          (data[name] || []).find((row) => row.id === id)
        );
      });

      return newState;
    });
  }, [distanceRange, signalRange, files]);

  const hasData = Object.keys(data).length > 0;
  return (
    <main>
      <div style={{ height: hasData ? "50%" : "100%" }}>
        <Map
          isOpen={hasData}
          distance={distance}
          points={Object.values(data)
            .flat()
            .filter(({ id }) => Object.values(selected).flat().includes(id))
            .map(({ columns }) => {
              const [, lat, lon, azimuth] = columns;
              return { lat: lat.value, lon: lon.value, azimuth: azimuth.value };
            })}
        />
        <div className="control-panel">
          <Space size={10} split="|">
            <CSVReader
              config={{ worker: true }}
              onUploadAccepted={onUploadAccepted}
            >
              {({ getRootProps }) => (
                <Button {...getRootProps()} icon={<UploadOutlined />}>
                  Browse file
                </Button>
              )}
            </CSVReader>
            {hasData && (
              <>
                <Select
                  value={distance}
                  onChange={(value) => setDistance(Number(value))}
                >
                  {[5, 10, 15, 20, 25, 50, 100].map((value) => (
                    <Option value={value}>{value} km</Option>
                  ))}
                </Select>
                <Select
                  value={distanceRange}
                  onChange={(value) => setDistanceRange(Number(value))}
                >
                  {[5, 10, 15, 20, 25, 50, 100].map((value) => (
                    <Option value={value}>{value} m</Option>
                  ))}
                </Select>
                <Select
                  value={signalRange}
                  onChange={(value) => setSignalRange(Number(value))}
                >
                  {Array.from({ length: 120 }, (_, i) => i + 1).map((value) => (
                    <Option value={value}>{value} signal</Option>
                  ))}
                </Select>
                <Checkbox onChange={(e) => setShowSignal(e.target.checked)}>
                  show signal
                </Checkbox>
              </>
            )}
          </Space>
        </div>
      </div>
      <div
        className={cn("table-block", {
          "table-block-open": hasData,
        })}
      >
        <Tabs
          hideAdd
          type="editable-card"
          onEdit={(fileName) => {
            setFiles((state) =>
              Object.keys(state).reduce((data, key) => {
                if (key !== fileName) {
                  return { ...data, [key]: state[key] };
                }
                return data;
              }, {})
            );
          }}
        >
          {Object.keys(data).map((fileName) => (
            <TabPane tab={fileName} key={fileName}>
              <div style={{ height: "100%" }}>
                <Table
                  showSignal={showSignal}
                  data={data[fileName]}
                  header={header[fileName]}
                  distanceRange={distanceRange}
                  selected={selected[fileName]}
                  onSelected={(selected) => {
                    setSelected((state) => ({
                      ...state,
                      [fileName]: selected,
                    }));
                  }}
                />
              </div>
            </TabPane>
          ))}
        </Tabs>
      </div>
    </main>
  );
};

export default App;
