const TableComponent = ({
  showSignal = false,
  data,
  selected,
  header,
  onSelected,
}) => {
  const onSelectRow = (id, checked) => {
    if (checked) {
      onSelected([...selected, id]);
    } else {
      onSelected(selected.filter((index) => index !== id));
    }
  };

  const cellRenderer = useCallback(
    ({ columnIndex, key, rowIndex, style }) => {
      const value =
        rowIndex === 0
          ? header[columnIndex - 1]
          : String(
              ((data[rowIndex - 1]?.columns || [])[columnIndex - 1] || {})
                .value || ""
            ).trim();
      return (
        <div
          key={key}
          className={classNames("table-cell", {
            "table-cell-header": rowIndex === 0,
          })}
          style={{
            ...style,
            backgroundColor:
              columnIndex > 4 && rowIndex > 0
                ? colors[
                    Number(value) >= colors.length
                      ? colors.length - 1
                      : Number(value)
                  ]
                : "#fff",
          }}
        >
          {(() => {
            if (columnIndex === 0) {
              if (rowIndex === 0) {
                return "#";
              }
              return rowIndex;
            }
            if (columnIndex === 1) {
              if (rowIndex !== 0) {
                return (
                  <Checkbox
                    checked={selected.includes(data[rowIndex - 1]?.id)}
                    onChange={(e) =>
                      onSelectRow(data[rowIndex - 1]?.id, e.target.checked)
                    }
                  />
                );
              }
              return null;
            }

            return !(columnIndex > 4 && rowIndex > 0)
              ? Number(value) % 1
                ? Number(value).toFixed(4)
                : value
              : showSignal
              ? value
              : null;
          })()}
        </div>
      );
    },
    [data, header, selected, showSignal]
  );

  return (
    <AutoSizer>
      {({ width, height }) => (
        <MultiGrid
          cellRenderer={cellRenderer}
          columnWidth={({ index }) => {
            if (index === 0) {
              return 50;
            }

            if (index === 1) {
              return 30;
            }

            if (index > 4) {
              return 35;
            }

            return 70;
          }}
          columnCount={header.length + 1}
          fixedColumnCount={5}
          fixedRowCount={1}
          enableFixedColumnScroll
          enableFixedRowScroll
          height={height}
          rowHeight={30}
          rowCount={data.length + 1}
          width={width}
          classNameBottomLeftGrid="table-bottom-left-grid"
          classNameTopLeftGrid="table-top-left-grid"
          classNameTopRightGrid="table-top-right-grid"
          hideTopRightGridScrollbar
          hideBottomLeftGridScrollbar
        />
      )}
    </AutoSizer>
  );
};

export default TableComponent;
